<template>
  <div class="axis">
    <div class="title">随便写写</div>
    <div class="timeline">
      <div v-for="item in line">
        <div class="tab"></div>
        <div class="card">
          <h2>{{item.title}}</h2>
          <p>{{item.content}}</p>
          <div>{{item.time}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'axis',
    data(){
    	return {
    		line:[
          {title: 'Hello World', content: '这个博客诞生了，HelloWorld！', time: '2018/11/27'},
          {title: 'The Second', content: '大致功能已想好，做一些新闻推荐，自己的记录等等...等会写技术博客后把自己的技术文章弄上去', time: '2018/11/27'},
          {title: 'The Third', content: '新闻推送已上线，用的是apiopen.top获取的数据', time: '2018/12/1'}
        ]
      }
    },
  }
</script>

<style scoped lang="scss" rel="stylesheet/scss">

  @import "~assets/scss/index.scss";

  .axis{
    .title{
      font-family: "Microsoft YaHei UI";
      font-size: 2rem;
      text-align: center;
      margin: 20px 0 50px 0;
    }
    .timeline{
      width: 100%;
      height: 100%;
      position: relative;
      margin-bottom: 50px;
      &::before{
        content: ' ';
        position: absolute;
        top: 10px;
        left: 14.6%;
        height: 100%;
        width: 4px;
        background: $axis-line;
      }
      .tab{
        height: 26px;
        width: 26px;
        position: relative;
        top: 30px;
        left: calc(14.6% - 11px);
        background-color: $axis-tab;
        border-radius: 50%;
      }
      .card{
        background-color: $axis-card;
        margin: 0 20px;
        padding: 10px;
        position: relative;
        top: 0;
        left: 18%;
        width: 500px;
        max-width: calc(82% - 60px);
        border-radius: 10px;
        &::before{
          content: ' ';
          height: 0;
          width: 0;
          position: absolute;
          left: -10px;
          top: 10px;
          border-top: 10px solid transparent;
          border-bottom: 10px solid transparent;
          border-right: 10px solid $axis-card;
        }
        h2{
          margin: 5px;
          color: #111111;
        }
        p{
          margin: 10px;
          font-size: 1rem;
          color: #333333;
        }
        div{
          margin: 10px;
          width: 90%;
          text-align: right;
          font-size: 0.8rem;
        }
      }
    }
  }

</style>



