<template>
  <div class="about">
    <p>
      {{content}}
    </p>
  </div>
</template>

<script>
  export default {
    name: 'about',
    data(){
      return{
        content:'关于我，曾经的产品经理要去做前端'
      }
    }
  }
</script>

<style scoped>
  p{
    display: block;
    box-sizing: border-box;
    width: 500px;
    height: 300px;
    line-height: 300px;
    text-align: center;
    vertical-align:middle;
    font-family: 微软雅黑;
    font-size: 24px;
    border: 1px solid #000;
    border-radius: 15px;
    position: absolute;
    box-shadow:5px 5px rgba(0,0,0,0.6);
    animation: trans 8s linear infinite;
  }
  p:hover{
    animation-play-state:paused;
  }

  @keyframes trans {
    0%{
      top:0%;
      left: 50%;
      margin-top: 0px;
      margin-left: -250px;
      }
    25%{
      top: 50%;
      left: 0%;
      margin-top: -150px;
      margin-left: 0px;
    }
    50%{
      top: 100%;
      left: 50%;
      margin-top: -300px;
      margin-left: -250px;
    }
    75%{
      top: 50%;
      left: 100%;
      margin-top: -150px;
      margin-left: -500px;
    }
    100%{
      top: 0%;
      left: 50%;
      margin-top: 0px;
      margin-left: -250px;
    }
  }

</style>



