<template>
  <div class="nav">
    <div class="head">
      <img :src="data.header">
    </div>
    <div class="name">{{data.name}}</div>
    <div class="tab">
      <!--判断是否高亮，点击传当前的数据过去然后判断由谁高亮-->
      <div :class="isTab == 0 ? 'action' : ''" @click="NavRight(0)">
        <img src="~assets/img/me.png"><li>博客主页</li>
      </div>
      <div :class="isTab == 1 ? 'action' : ''" @click="NavRight(1)">
        <img src="~assets/img/essay.png"><li>个人随笔</li>
      </div>
      <div :class="isTab == 2 ? 'action' : ''" @click="NavRight(2)">
        <img src="~assets/img/about.png"><li>关于自己</li>
      </div>
    </div>
    <div class="other">
      <div>
        <a target="_blank"
           href="http://wpa.qq.com/msgrd?v=3&uin=511383637&site=qq&menu=yes"><img src="~assets/img/qq.png">
        </a>
      </div>
      <div>
        <a target=_blank
           href=mailto:w_haiyu@foxmail.com><img src="~assets/img/email.png"></a>
      </div>
    </div>
  </div>
</template>

<script>

  export default {
    name: 'nav',
    data(){
    	return {
    		isTab: -1,
        tab: -1,
        data: {
          "header": "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1543812952603&di=507d8289bf4e98f9f7decf9f245442d5&imgtype=0&src=http%3A%2F%2F5b0988e595225.cdn.sohucs.com%2Fimages%2F20181028%2Fa8d9363c77f14b6f9083447c13486262.jpeg",
          "banner": "http://www.xiedashuaige.cn:3000/banner.jpg",
          "name": "I'm 海宇",
          "msg": "欢迎来到我的博客"
        }
      }
    },
    methods: {
    	//判断当前高亮
      NavRight(index){
        this.tab = index;
        this.$root.Bus.$emit('isbody',index)
      }
    },

    beforeDestroy(){
      this.$root.Bus.$off('disTab')
    }
  }
</script>

<style scoped lang="scss" rel="stylesheet/scss">

  @import "~assets/scss/index.scss";

  .nav{
    width: $nav-left;
    height: 100%;
    background-color: $nav-lift-bg;
    position: absolute;
    text-align: center;
    z-index: -1;
    @include border();
    border-right:1px solid #aaaaaa;
    overflow-y: scroll;
    @include slider2();
    .head{
      position: relative;
      margin-top: 50%-$gold*100%/2;
      margin-left: 50%-$gold*100%/2;
      width:$gold*100%;
      border-radius: 50%;
      overflow: hidden;
      background-color: $head;
      &:after {
        content: '';
        display: block;
        margin-top: 100%; //margin 百分比相对父元素宽度计算
      }
      img{
        height: 100%;
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    .name{
      font-family: "Microsoft YaHei UI";
      margin-top: 20px;
      font-size: 1.8rem;
    }
    //中等屏幕加大名字
    @media screen and (max-width: 870px) and (min-width: 767px) {
      .name{
        font-size: 1.2rem;
      }
    }
    //手机没有名字
    @media screen and (max-width: 766px) {
      .name{
        display: none;
      }
    }
    .tab{
      margin-top: 20px;
      font-size: 1.2rem;
      line-height: 40px;
      img{
        width: 20px;
        height: 20px;
        display: inline-block;
      }
      li{
        margin-left: 5px;
        vertical-align: middle;
        display: inline-block;
      }
      //中等屏幕去掉图片
      @media screen and (max-width: 870px) and (min-width: 767px) {
        img{
          display: none;
        }
      }
      //手机屏幕去掉文字
      @media screen and (max-width: 766px) {
        li{
          display: none;
        }
      }
      div:hover{
        background-color: $nav-hover;
      }
    }
    .other{
      width: 100%;
      position: absolute;
      bottom: 5px;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      img{
        margin-top: 15px;
        width: 35px;
        height: 35px;
      }
      @media screen and (min-width: 1144px) {
        div:nth-child(2n){
          margin-left: 5px;
        }
      }
      @media screen and (min-width: 766px) {
        img:nth-child(2n){
          margin-left: 5px;
        }
      }
      @media screen and (max-width: 766px) {
        img{
          width: 30px;
          height: 30px;
        }
      }
    }
    @media screen and (max-height: 400px){
      .other{
        display: none;
      }
    }
  }
  @media screen and (max-width: 766px) {
    .nav{
      //display: none;!!!!!!!!!!!!!!!!!!!!!!!!!
    }
  }
  .action{
    background-color: $nav-action;
  }
</style>



