import Vue from 'vue'
import Router from 'vue-router'
import axis from 'components/content/axis.vue';
import about from 'components/content/about.vue';
import home from 'components/content/home.vue';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/axis',
      name: 'axis',
      component: axis
    },
    {
      path: '/about',
      name: 'about',
      component: about
    }
  ]
})



